:root {
    --cover-timing: 0.5s;
    --cover-ease: cubic-bezier(0.66, 0.08, 0.19, 0.97);
    --cover-stagger: 0.15s;

    --text-timing: .75s;
    --text-stagger: 0.015s;
    --text-ease: cubic-bezier(0.38, 0.26, 0.05, 1.07);

    --title-stagger: 0.05s;
    --highlight: white;
}

.Box {
    width: 90vw;
    max-width: 300px;
    /* width: 290px; */
    /* height: 270px; */
    /* background-color: red; */
    margin: 10px;
    margin-top: 10px;



}

.card {
    position: relative;
    overflow: hidden;
    aspect-ratio: 9/12;
    display: flex;
    flex-direction: column;

    border-radius: 7px;
    box-shadow: rgba(255, 255, 255, 0.3) 0 5vw 6vw -8vw,
        rgba(255, 255, 255, 0) 0 4.5vw 5vw -6vw,
        rgba(50, 50, 80, 0.5) 0px 4vw 8vw -2vw, rgba(0, 0, 0, 0.8) 0px 4vw 5vw -3vw;

    transition: box-shadow 1s var(--cover-ease);

    &>* {
        z-index: 2;
    }

    &>img {
        z-index: 0;
        transition: all 0.8s cubic-bezier(0.66, 0.08, 0.19, 0.97);
    }

}




.card::before,
.card::after {
    content: "";
    width: 100%;
    height: 50%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    transform-origin: left;
    transform: scaleX(0);
    transition: all var(--cover-timing) var(--cover-ease);
    z-index: 1;
}

.card::after {
    transition-delay: var(--cover-stagger);
    top: 50%;
}

.text {
    position: absolute;
    inset: 20px;
    top: auto;
    transition-delay: calc(0.1s + var(--word-index) * var(--text-stagger));


}

h2 {
    font-size: 30px;
    font-size: clamp(20px, 4vw, 40px);
    font-weight: 800;
    margin-bottom: 0.2em;
}

p {
    font-size: 12px;
    font-size: clamp(10px, 1.25vw, 14px);
    line-height: 1.4;
    text-align: justify;
    margin-top: 0.2em;
    margin-bottom: 0;
    transition-delay: calc(0.1s + var(--word-index) * var(--text-stagger));
}

h2 .char,
p .word {
    color: var(--highlight);
    display: inline-block;
    opacity: 0;
    position: relative;
    transform: translateY(20px);
    transition-property: transform, opacity, color;
    transition-timing-function: var(--text-ease);
    transition-duration: var(--text-timing), var(--text-timing),
        calc(var(--text-timing) * 2);
}

img {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 7px;
}

.card:hover,
.card:focus {
    box-shadow: white 0 5vw 6vw -9vw, var(--highlight) 0 5.5vw 5vw -7.5vw,
        rgba(50, 50, 80, 0.5) 0px 4vw 8vw -2vw,
        rgba(0, 0, 0, 0.8) 0px 4vw 5vw -3vw;

    &::before,
    &::after {
        transform: scaleX(1);
    }

    & h2 .char,
    & p .word {
        opacity: 1;
        transform: translateY(0);
        color: inherit;
    }

    & h2 {
        transition-delay: calc(0.1s + var(--char-index) * var(--title-stagger));
        position: relative;
        top: 10px;
        text-decoration: underline;
        color: #56ffe5;
    }

    & p {
        margin: 2px;

        font-size: 20px;
        color: aliceblue;
    }

    & img {
        transform: scale(1.1);
    }

    & .text {
        position: absolute;
        top: 0;

        text-align: center;
    }
}

.Box:nth-of-type(1) {
    --highlight: coral;
}

.Box:nth-of-type(2) {
    --highlight: #56ffe5;
}


@media (max-width:749px) {

    .Box {

        width: 33vw;
    }
}

@media (max-width:426px) {

    h2 {
        font-size: 17px;
    }

    p {
        font-size: 12px;
        display: none;
    }

    .card:hover{
    
    & p {
        display: block;
        font-size: 12px;
        text-align: left;
        margin-top: 15px;
    }
    }
}