@import url(//fonts.googleapis.com/css?family=Lato:300:400);

@import url('https://fonts.googleapis.com/css2?family=Aclonica&display=swap');


body {
  margin: 0;
  height: 100vh;
}

h1 {
  /* font-family: 'Lato', sans-serif; */
  font-family: 'Aclonica', sans-serif;
  font-weight: 300;
  letter-spacing: 2px;
  font-size: 48px;
}



p {
  font-family: 'Lato', sans-serif;
  letter-spacing: 1px;
  font-size: 14px;
  color: #333333;
}

.header {
  position: relative;
  text-align: center;
  background: linear-gradient(60deg, rgba(84, 58, 183, 1) 0%, rgba(0, 172, 193, 1) 100%);
  color: white;
  /* height: 500px; */
}


.inner-header {
  height: 65vh;
  width: 100%;
  margin: 0;
  padding: 0;
}

.flex {
  /*Flexbox for containers*/
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.Logo {

  position: relative;
  width: 300px;
  height: auto;
  object-fit: cover;
}

.Bothimg {

  position: absolute;
  top: 0px;
  left: 15px;
  width: 190px;
  height: 120px;
  margin: 10px;
  padding: 0px;
}


.waves {
  position: relative;
  width: 100%;
  height: 15vh;
  margin-bottom: -7px;
  /*Fix for safari gap*/
  min-height: 100px;
  max-height: 150px;
}

.content {
  position: relative;
  height: 20vh;
  text-align: center;
  background-color: white;
}

/* Animation */

.parallax>use {
  animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
}

.parallax>use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}

.parallax>use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}

.parallax>use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}

.parallax>use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}

@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }

  100% {
    transform: translate3d(85px, 0, 0);
  }
}

/*Shrinking for mobile*/



@media (max-width:1024px) {
  .Logo {
    width: 300px;
    height: auto;
  }
}



@media (max-width: 768px) and (min-width:427px){
  .waves {
    height: 40px;
    min-height: 40px;
  }

  .content {
    height: 30vh;
  }

  h1 {
    font-size: 24px;
  }

  .Logo {
    width: 230px;
    height: auto;
  }
  .Bothimg{
    width: 130px;
    height: 80px;
  }
}


@media (max-width:426px) {
  .Bothimg{
    width: 95px;
    height: 47px;
    /* margin: 9px; */
    left: 0px;
  }
}